.productsCatalog {
    height               : 100%;
    display              : grid;
    grid-template-columns: 300px 1fr;
    grid-template-rows   : minmax(50px, auto) 1fr;

    grid-template-areas:
        "header header"
        "sidebar products";
}

.header {
    grid-area       : header;
    background-color: burlywood;
}

.sidebar {
    grid-area       : sidebar;
    background-color: cornsilk;

}

.products {
    grid-area       : products;
    background-color: darkorange;
}